@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,400,600,700,900&amp;lang=en);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Fonts */

/* general */

* {
    font-family: 'Ubuntu' !important;
}

body {
  margin: 0;
  font-family: 'Ubuntu',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen','Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-repeat: no-repeat;
}

h1,h2,h3,h4,h5,h6 {
  color: #f9f9f9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.fs-7 {
  font-size: .9rem !important;
}

.fs-8 {
  font-size: .8rem !important;
}

.fs-9 {
  font-size: .7rem !important;
}

.bg-orange {
  background-color:#F96666 !important;
  border-color: #F96666 !important;
}

.fs-01 {
  font-size: 60px;
  line-height: 90px;
}

.bg-custom-1 {
  background-image: conic-gradient(#212529 30%, #2a2d31 40%, #31353a 70%, #272a2e 80%, #212529) !important;
}

.cover-title {
  background-image: conic-gradient(#c94bf0 30%, #ff5050 40%, #e5ff50 70%, #00cb8e 80%, #9125da) !important;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
}

.border-dotted {
  border:dashed 3px !important;  
}

.desktop-vh-100 {
  min-height: 80vh !important;
}

/* mobile screens */
@media only screen and (max-width: 600px) {

  .desktop-vh-100 {
    height: 'auto'
  }

  .fs-01 {
    font-size: 40px;
    line-height: 60px;
  }

  .mobile-center {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  .mobile-pt-1 {
    padding-top: 16px !important;
  }

  .mobile-block {
    display: block;
    height: -webkit-max-content !important;
    height: max-content !important;
  }
  
}
